  background-color: #fff;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: 10px;
  display: flex;
  position: absolute;
  bottom: 0;
}

.info-item {
  color: #000;
  max-width: 80px;
  flex-direction: column;
